import * as d3 from 'd3';

export function CollapsibleTree(rawData, data, colors) {
  const height = 1100;
  const width = 1000;
  const radius = width / 2;
  const marginTop = 100;
  const marginLeft = 100;
  console.log(data);
  const tree = d3.tree()
    .size([2 * Math.PI, radius])
    .separation((a, b) => (a.parent == b.parent ? 1 : 2) / a.depth);
  let root = tree(d3.hierarchy(data));
  var svg = d3.select("#vis").append("svg")
    .attr("width", width)
    .attr("height", height)
    .attr("viewBox", [-marginLeft - radius / 1.5, -marginTop - radius / 1.5, width, height]);

  var g = svg.append("g");
  // .attr("transform",'translate('+width/2+','+height/2+')')

  const linkgroup = g.append("g")
    .attr("fill", "none")
    .attr("stroke-opacity", 0.4)
    .attr("stroke-width", 1.5);

  const nodegroup = g.append("g")
    .attr("stroke-linejoin", "round")
    .attr("stroke-width", 3);

  function newdata(data, animate = true) {
    let root = tree(d3.hierarchy(data));
    let links_data = root.links();
    let links = linkgroup
      .selectAll("path")
      .data(links_data, d => d.source.data.name + "_" + d.target.data.name);

    links.exit().remove();

    let newlinks = links
      .enter()
      .append("path")
      .attr("d", d3.linkRadial()
        .angle(d => d.x)
        .radius(0.1))
      .attr("stroke", d => d.target.data.color)
      .transition()
      .duration(2000)
      .attr("stroke-dashoffset", 0);

    let t = d3.transition()
      .duration(animate ? 400 : 0)
      .ease(d3.easeLinear)
      .on("end", function () {
        const box = g.node().getBBox();
        svg.transition().duration(1000).attr("viewBox", `${box.x - 25} ${box.y} ${box.width + 100} ${box.height}`);
      });

    let allinks = linkgroup.selectAll("path");
    allinks
      .transition(t)
      .attr("d", d3.linkRadial()
        .angle(d => d.x)
        .radius(d => d.y));

    let nodes_data = root.descendants().reverse();
    let nodes = nodegroup
      .selectAll("g")
      .data(nodes_data, function (d) {
        if (d.parent) {
          return d.parent.data.name + d.data.name;
        }
        return d.data.name;
      });

    nodes.exit().remove();

    let newnodes = nodes
      .enter().append("g");

    let allnodes = animate ? nodegroup.selectAll("g").transition(t) : nodegroup.selectAll("g");
    allnodes
      .attr("transform", d => `
        rotate(${d.x * 180 / Math.PI - 90})
        translate(${d.y},0)
      `);

    newnodes.append("circle")
      .attr("r", 0);
    // .on("click", function (event, d) {
    //   let altChildren = d.data.altChildren || [];
    //   let children = d.data.children;
    //   d.data.children = altChildren;
    //   d.data.altChildren = children;
    //   newdata();
    // });

    nodegroup.selectAll("g circle")
      .attr("fill", d => d.data.color)
      .attr("r", 3);

  }

  function formatData(data) {
    const newData = data.map(obj => ({ ...obj, color: colors[obj.type] }));

    var dataMap = newData.reduce(function (map, node) {
      map[node.id] = node;
      return map;
    }, {});

    var treeData = [];
    newData.forEach(function (node) {
      var parent = dataMap[node.parent];
      if (parent) {
        (parent.children || (parent.children = []))
          .push(node);
      } else {
        treeData.push(node);
      }
    });

    const treeMap = { id: "HSS Camp", color: 'white', children: treeData };
    return treeMap;

  }

  const divs = ['service', 'provider', 'patient', 'labs', 'visit', 'diagnoses'];
  let activeLayers = [];
  for (let i = 0; i < divs.length; i++) {
    const type = divs[i];
    const div = document.querySelector("#" + type);
    div.addEventListener('click', function () {
      const index = activeLayers.indexOf(type);
      if (index == -1 && activeLayers.length >= i) {
        activeLayers.push(type);
        div.style.opacity = .8;
        div.style.background = colors[type];
        const localData = rawData.filter(function (obj) {
          if (obj) {
            return activeLayers.includes(obj.type);
          }
        });
        const map = formatData(localData);
        newdata(map, true);
      } else if (index != -1 && (activeLayers.length == i + 1)) {
        activeLayers.splice(index, 1);
        div.style.opacity = 1;
        div.style.background = 'grey';
        const localData = rawData.filter(function (obj) {
          return activeLayers.includes(obj.type);
        });
        const map = formatData(localData);
        newdata(map, true);
      }
    });
  }
}