import { RadialTree } from "./RadialTree.js";
import { CollapsibleTree } from "./collapsibleTree.js";
import { tree } from "d3";
import { forceGraph } from "./forceGraph.js";

const colors = {
  '': [255, 255, 255], // :0 white
  HSS: [255, 255, 255], // :0 white
  CAMP: [255, 255, 255], // :0 white
  service: 'red', // :1 red
  provider: 'purple', // 2: purple
  patient: 'green', // 5: green
  labs: 'gray', //3 : dark grey
  visit: 'teal', // :4 teal
  diagnoses: 'gold', // 6: gold
};

let data = require('./sampleData_flower4.json');
function formatData(data) {
  const newData = data.map(obj => { if (obj !== null) return { ...obj, color: colors[obj.type] }; });

  var dataMap = newData.reduce(function (map, node) {
    if (node != null) {
      map[node.id] = node;
    }
    return map;
  }, {});

  var treeData = [];
  newData.forEach(function (node) {
    if (node) {
      var parent = dataMap[node.parent];
      if (parent) {
        (parent.children || (parent.children = []))
          .push(node);
      } else {
        treeData.push(node);
      }
    }
  });

  const treeMap = { id: "HSS Camp", color: 'white', children: treeData };
  return treeMap;

}

const map = formatData(data);
console.log(map);

//forceGraph(map, { height: 1000, width: 1000 });

CollapsibleTree(data, map, colors);

